const encoded = "data:audio/mpeg;base64,";

import { writable } from "svelte/store";

export const audio = new Audio(encoded);

const tips = writable({
  save_method: {
    text: "Default save method is triggered by the text editor blur event. This can be changed in settings",
    active: true,
    dismiss: true,
    timeout: Infinity,
    sound: true
  }
});

const isTips = localStorage.getItem("__idxe_tips");
if (isTips) tips.set(JSON.parse(isTips));

tips.subscribe(value => {
  localStorage.setItem("__idxe_tips", JSON.stringify(value));
});

export default tips